<template>
  <div class="songContainer">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="songList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <div v-if="songList != ''">
      <div class="bg">
        <img :src="require(`../../assets/image/${area}.png`)">
      </div>
      <el-card class="box-card">
      <div class="header">
        <div class="playBtn">
          <div class="left">
            <div class="left-icon">
              <span @click="playAll" class="iconfont icon-bofang"></span>
            </div>
            <span>播放全部</span>
            <span class="count">({{songList.length}})</span>
          </div>
          <span class="iconfont icon-icon-test"></span>
        </div>
      </div>
      <div class="content">
        <div v-for="(item,index) in songList" :key="item.id" class="song">
          <div class="item">
            <div class="left" @click="playThis(item.id,index)">
              <img :src="item.picUrl+'?param=200y200'" alt="" />
              <div class="text">
                <p class="name">{{ item.name }}</p>
                <p class="singer">{{ item.artists }}</p>
              </div>
            </div>
            <div class="icon">
              <span class="iconfont icon-bofang"></span>
              <span class="iconfont icon-liebiao-dian"></span>
            </div>
          </div>
          <div class="divider"></div>
        </div>
      </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { getNewSongs } from "../../NetRequest/discover";
import { findItem } from "../../common/common"
export default {
  data() {
    return {
      songList: [],
      hasClcikMusicId:''
    };
  },
  mounted() {
    // console.log(this.key);
    setTimeout(() => {
      this.getSongList();
    }, 1000);
  },
  methods: {
    async getSongList() {
      const res = await getNewSongs(this.area);
      // console.log(res);
      for (const item of res.data.data) {
        this.songList.push({
          id: item.id,
          name: item.name,
          artists: item.artists[0].name,
          picUrl: item.album.picUrl,
        });
      }
      // console.log(this.songList);
    },
    /* 点击播放歌曲 */
    playThis(musicId,i){
      // console.log(musicId);
      this.$store.state.playShow = true
      const index = findItem(this.$store.state.playList,'id',musicId)
      if(index != -1){//如果播放列表有这首歌
        if(this.hasClcikMusicId == musicId){
          //如果被点击这首歌已经被点击过且正在播放
          return
        }
        this.$bus.$emit('receiveMusicId',musicId,index)
      } else{//播放列表没有这首歌
        const newIndex = this.$store.state.playList.length
        this.$store.state.playList.push(this.songList[i])
        // console.log(this.$store.state.playList);
        this.$bus.$emit('receiveMusicId',musicId,newIndex)
        this.hasClcikMusicId = musicId
      }
    },
    /* 播放全部 */
    playAll(){
      this.$store.state.playList=this.songList
      this.$toast('已添加到播放列表');
      setTimeout(()=>{
        this.$store.state.playShow = true
        this.$bus.$emit('receiveMusicId',this.songList[0].id,0)
        },1000)
      
      localStorage.removeItem('newSong')
      localStorage.setItem("newSong", JSON.stringify(this.songList));
    }
  },
  props: {
    area: {
      type: String,
    },
  },
};
</script>

<style scoped>
.songContainer {
  /* padding: 15px; */
  margin-bottom: 50px;
}
.loading{
  margin-left: 40%;
}
.playBtn {
  padding: 10px 0;
  font-size: 14px;
  display: flex;
  margin-top: -2px;
  justify-content: space-between;
  margin-bottom: 15px;
  background-color: #fff;
}
.bg img{
  width: 100%;
}
.bg{
  position: relative;
  z-index: -1;
  margin-top: 10px;
}
.box-card{
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin-top: -15px;
  z-index: 10;
}
.left {
  display: flex;
  justify-content: left;
}
.icon-bofang {
  font-size: 20px;
  margin-right: 10px;
}
.icon-caret_right {
  font-size: 10px;
  color: #eee;
}
.icon-icon-test {
  font-size: 20px;
  margin-right: 5px;
}
.count{
  font-size: 10px;
  color: #999;
  padding-top: 3px;
}
.song .item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 60px;
}
.item img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  margin-right: 15px;
}
.item .left{
  display: flex;justify-content: left;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.singer {
  font-size: 13px;
  color: rgb(149, 149, 149);
}
.icon {
  line-height: 60px;
  text-align: center;
}
.icon .iconfont:nth-child(1) {
  margin-right: 15px;
}
.icon .iconfont {
  font-size: 20px;
  color: #999;
}
</style>